.Services-page {
    margin-left: 250px; 
    width: calc(100% - 250px); 
    position: fixed;
    margin-top: -35px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
  }
.Services-page-header {
    justify-content: space-between;
    align-items: relative;
    height:calc(100%-51px);
    display: flex;
}

.Services-page-header h2 {
    font-size: 24px;
    color: #22285E;
}



.Services-page-breadcrumb {
    height:calc(100%-51px);
    font-size: 14px;
    text-align: left;
    padding: 15px; 
    background-color: #FFFF;
    border: 1px solid #EAEAEA;
    color: #999BA0;
    line-height: 20px;
}
