.useradmin-page {
    margin-left: 250px; 
    width: calc(100% - 250px); 
    position: fixed;
    margin-top: -35px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
  }
.useradmin-header {
    justify-content: space-between;
    align-items: relative;
    height:calc(100%-51px);
    display: flex;
}

.useradmin-header h2 {
    font-size: 24px;
    color: #22285E;
}



.useradmin-breadcrumb {
    height:calc(100%-51px);
    font-size: 14px;
    text-align: left;
    padding: 15px; 
    background-color: #FFFF;
    border: 1px solid #EAEAEA;
    color: #999BA0;
    line-height: 20px;
}

.user-table {
    width: 100%;
    border-collapse: collapse;
}

.user-table th, .user-table td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #EAEAEA;
}

.user-table th {
    background-color: #F5F5F5;
    color: #22285E;
}

.user-table tbody tr:hover {
    background-color: #F5F5F5;
}

.user-table td {
    color: #565D6D;
    font-size: 14px;
}
.detail-button {
    display: inline-block;
    text-align: center;
    color: white;
    background-color: #4A90E2; /* Light blue background */
    border-radius: 3px;
    width: 64px;
    height: 30px;
    line-height: 30px;
    text-decoration: none; /* Remove underline */
    position: relative;
    transition: background-color 0.3s ease; /* Smooth hover effect */
    cursor: pointer; /* Change cursor to pointer */
}

.detail-button:hover {
    background-color: #357ABD; /* Darker blue on hover */
    text-decoration: none; /* Keep text decoration off */
}

.detail-button:active {
    background-color: #2C6EAB; /* Slightly darker when clicked */
}
