
.header {
    position: fixed; 
    width: calc(100% - 250px);
    height: 55px;
    left: 296px;
    top: 0px;
    background: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0px;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
    border-top: 1px solid #EAEAEA;
    border-bottom:1px solid #EAEAEA;
    z-index: 1000; /* Ensure it appears above other content */
    left: 250px; /* Start after the sidebar */
    margin: 0; 
  }
  
  .menu-icon {
    cursor: pointer;
    font-size: 24px;
  }
  
  .page-title {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    color: rgba(0, 0, 0, 0.51);
    margin-left: 56px;
  }
  
  .search-box {
    position:absolute;
    left: 150px;
    width: 300px;
    height: 30px;
    background: #FFFFFF;
    border-radius: 22px;
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  }
  
  .search-icon {
    position: absolute;
    left: 16px;
    color: #7A7E9E;
  }
  .toggle-icon{
    padding: 7px;
    color: #7A7E9E;
  }
  
  .search-input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    padding-left: 48px;
    font-size: 14px;
    color: #7A7E9E;
    font-family: 'Poppins', sans-serif;
    background: transparent;
  }
  
  .user-info {
    display: flex;
    align-items: center;
  }
  
  .user-details {
    margin-right: 16px;
    text-align: right;
  }
  
  .user-name {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    color: #22285E;
    text-align: left;
    /* padding-bottom: 0px; */
  }
  
  .user-email {
    font-family: 'Poppins', sans-serif;
    font-size: 12px;
    color: #7A7E9E;
  }
  
  .user-picture {
    width: 40px;
    height: 44px;
    background-image: url('../assets/profile.png'); /* Update with actual image path */
    background-size: cover;
    background-position: center;
    border-radius: 50%;
  }
  
