.business-page {
    margin-left: 250px;
    width: calc(100% - 250px);
    padding: 20px;
    margin-top: -35px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: #F4F8FF;
}

.business-header {
    justify-content: space-between;
    align-items: relative;
    display: flex;
    margin-bottom: 20px;
}

.business-header h2 {
    font-size: 24px;
    color: #22285E;
}

.business-breadcrumb {
    font-size: 14px;
    text-align: left;
    padding: 10px;
    background-color: #FFFF;
    border: 1px solid #EAEAEA;
    color: #999BA0;
    margin-bottom: 20px;
}

.business-lists {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.business-list-link {
    font-size: 16px;
    padding: 10px 15px;
    background-color: #4A90E2;
    color: white;
    text-decoration: none;
    border-radius: 3px;
    transition: background-color 0.3s ease;
}

.business-list-link:hover {
    background-color: #357ABD;
}

.business-table {
    width: 100%;
    border-collapse: collapse;
    background-color: white;
    border: 1px solid #EAEAEA;
}

.business-table th, .business-table td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #EAEAEA;
}

.business-table th {
    background-color: #F5F5F5;
    color: #22285E;
}

.business-table td {
    color: #565D6D;
    font-size: 14px;
}

.business-table tbody tr:hover {
    background-color: #F5F5F5;
}
