body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  
  font-family: Arial, sans-serif;
}

.login-container {
  display: flex;
  height: 100vh;
  width: 100%; 
}

.login-image {
  flex: 0.4; 
  background-image: url('../assets/background.png'); 
  background-size: cover; 
  background-position: center; 
  background-repeat: no-repeat; 
  height: 100%; 
}




  .forgot-password-logo {
    position: absolute; 
    top: 5px; 
  }
  
  .forgot-password-form {  
    flex: 0.6; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    padding: 10px;
    box-sizing: border-box;
    background-color: #4A4F77; 
    height: 100%; 
  }
  
  h2 {
    margin-bottom: 40px;
    position: relative; /* Allows the use of 'top' property */
    top: -40px;
  }
  
  form {
    width: 350px;
    height: 45px; 
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  label {
    width: 100%;
    margin-bottom: 10px;
    color: #ffffff;
  }
  
  .email-input {
    width: 350px;
    height: 45px; 
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: 16px;
    margin-top: 5px;
  }
  
  .reset-button {
    width: 350px;
    height: 45px; 
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: #ff7f50;
    cursor: pointer;
    color: #ffffff;
    box-sizing: border-box;
    font-size: 16px;
    margin-top: 10px;
  }

  .reset-button:hover {
    background-color: #ff6a30; 
  }
