.dashboard-page {
    margin-left: 250px; 
    width: calc(100% - 250px); 
    position: fixed;
    margin-top: -35px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
  }
.dashboard-page-header {
    justify-content: space-between;
    align-items: relative;
    height:calc(100%-51px);
    display: flex;
}

.dashboard-page h2 {
    font-size: 24px;
    color: #22285E;
}



.dashboard-page-breadcrumb {
    height:calc(100%-51px);
    font-size: 14px;
    text-align: left;
    padding: 15px; 
    background-color: #FFFF;
    border: 1px solid #EAEAEA;
    color: #999BA0;
    line-height: 20px;
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 20px;
    padding-top: 15px;
}

.stat-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.stat-card h3 {
    margin-bottom: 10px;
    color: #333;
}

.stat-card p {
    font-size: 24px;
    margin-bottom: 5px;
}

.stat-card span {
    font-size: 14px;
    color: #888;
}

.charts {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 20px;
}
